.header {
  padding: 1.5625em 0;
  &__inner {
    display: flex;
    align-items: center;
  }
  &__left {
    margin-right: 3.125em;
  }
  &__right {
    width: calc(100% - 11.4375em);
    display: flex;
    align-items: center;
  }
  &__logo {
    display: block;
    width: 8.3125em;
    img {
      width: 100%;
    }
  }
  &__desc {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__list {
    display: flex;
    align-items: center;
    li {
      font-weight: 500;
      font-size: 1.0625em;
      line-height: 120%;
      color: #19304C;
      &:not(:first-child) {
        margin-left: 2.8125em;
      }
      &:hover {
        color: #E2349D;;
      }
    }
  }
  &__btns {
    display: flex;
    align-items: center;
  }
  &__btn {
    width: 8.875em;
    height: 3.125em;
    span {
      font-weight: 500;
      font-size: 1.0625em;
      line-height: 120%;
    }
  }
  &__btn2 {
    font-weight: 500;
    font-size: 1.0625em;
    line-height: 120%;
    color: #19304C;
    margin-left: 2.8125em;
  }
  &__burger {
    width: 2.0625em;
    height: 1.4375em;
    position: relative;
    z-index: 7;
    cursor: pointer;
    margin-left: 2.8125em;
    display: none;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 0.1875em;
      background: #19304C;
      transition: all 0.5s;
      &:nth-child(1) {
        top: 0;
      }
      &:nth-child(2) {
        top: 50%;
        transform: translate(0, -50%);
      }
      &:nth-child(3) {
        bottom: 0;
      }
    }
    &--active {
      span {
        background: #ffffff;
        &:nth-child(1) {
          top: 0.625em;
          transform: rotate(-45deg);
        }
        &:nth-child(2) {
          display: none;
        }
        &:nth-child(3) {
          bottom: 0.625em;
          transform: rotate(45deg);
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__burger {
      display: block;
    }
    &__desc {
      justify-content: flex-end;
    }
    @media screen and (min-width: 576px) {
      &__list {
        position: fixed;
        z-index: 5;
        top: 0;
        left: -105%;
        width: 100%;
        height: 100%;
        background: #19304C;
        padding: 6.25em 0.9375em 1.875em;
        flex-direction: column;
        transition: all 0.5s;
        &--active {
          left: 0;
        }
        li {
          color: #ffffff;
          &:not(:first-child) {
            margin: 1.5625em 0 0 0;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__inner {
      justify-content: space-between;
    }
    &__left {
      margin-right: 1.875em;
    }
    &__right {
     width: unset;
    }
    &__desc {
      position: fixed;
      z-index: 5;
      top: 0;
      left: -105%;
      width: 100%;
      height: 100%;
      background: #19304C;
      padding: 6.25em 0.9375em 1.875em;
      justify-content: start;
      flex-direction: column;
      transition: all 0.5s;
      &--active {
        left: 0;
      }
    }
    &__list {
      flex-direction: column;
      li {
        color: #ffffff;
        &:not(:first-child) {
          margin: 1.5625em 0 0 0;
        }
      }
    }
    &__btns {
      flex-direction: column;
      margin-top: 1.5625em;
    }
    &__btn2 {
      color: #ffffff;
      margin: 1.5625em 0 0 0;
    }
    &__burger {
      margin-left: 0;
    }
  }
}