.footer {
  background: #19304C;
  padding: 9.375em 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    margin-left: 1.5625em;
  }
  &__info {
    &-top {
      display: flex;
      align-items: center;
    }
    &-bottom {
      margin-top: 2em;
    }
  }
  &__logo {
    display: block;
    width: 8.3125em;
    img {
      width: 100%;
    }
  }
  &__rights {
    font-weight: 500;
    font-size: 1.0625em;
    line-height: 120%;
    color: #FFFFFF;
    margin-left: 2em;
  }
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    li {
      font-weight: 500;
      font-size: 0.6875em;
      line-height: 120%;
      color: #FFFFFF;
      opacity: 0.5;
      &:not(:first-child) {
        margin-left: 1.4375em;
      }
      &:hover {
        color: #e2349d;
        opacity: 1;
      }
    }
  }
  &__list-mob {
    justify-content: space-between;
    align-items: center;
    margin-top: 0.75em;
    display: none;
    li {
      font-weight: 500;
      font-size: 0.6875em;
      line-height: 120%;
      color: #FFFFFF;
      opacity: 0.5;
      &:not(:first-child) {
        margin-left: 2.1875em;
      }
      &:hover {
        color: #e2349d;
        opacity: 1;
      }
    }
  }
  &__connection {
    margin-left: 2.8125em;
  }
  &__tel {
    display: block;
    font-weight: 500;
    font-size: 0.875em;
    line-height: 120%;
    color: #FFFFFF;
    &:hover {
      color: #e2349d;
    }
  }
  &__mail {
    display: block;
    font-weight: 500;
    font-size: 0.875em;
    line-height: 120%;
    color: #FFFFFF;
    margin-top: 0.625em;
    &:hover {
      color: #e2349d;
    }
  }
  &__location {
    display: block;
    font-weight: 500;
    font-size: 0.875em;
    line-height: 120%;
    color: #FFFFFF;
    margin-top: 0.625em;
    &:hover {
      color: #e2349d;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
  }
  &__social {
    max-width: 2em;
    &:not(:first-child) {
      margin-left: 2.8125em;
    }
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    padding: 3.75em 0;
    &__rights {
      font-size: 0.875em;
    }
    &__social {
      max-width: 1.25em;
      &:not(:first-child) {
        margin-left: 1.25em;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &__info-bottom {
      display: none;
    }
    &__connection {
      display: none;
    }
    &__list-mob {
      display: flex;
    }
  }
}