.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-pink {
  background: #E2349D;
  border-radius: 8px;
  color: #FFFFFF;
  &:hover {
    background: darken(#E2349D, 10%);
  }
}
.display2 {
  font-weight: 600;
  font-size: 2.125em;
  line-height: 133.4%;
  color: #19304C;
  span {
    color: #E2349D;
  }
}

@media screen and (max-width: 992px) {
  .display2 {
    font-size: 1.6875em;
  }
}

@media screen and (max-width: 576px) {
  .display2 {
    font-size: 1.1875em;
  }
}