.main {
  overflow: hidden;
  &__inner {
    min-height: 42.5em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(106.13deg, #19304C 8.9%, #793273 103.59%);
    border-radius: 1.25em;
    padding: 0.9375em 0 2.5em 6.25em;
  }
  &__left {
    width: 38.75em;
    display: grid;
    grid-template-columns: 1fr;
    gap: 1.875em;
    margin-right: 1.875em;
  }
  &__right {
    width: calc(100% - 40.625em);
  }
  &__subtitle {
    font-size: 1.5625em;
    line-height: 133.4%;
    color: #FFFFFF;
    opacity: 0.5;
  }
  &__title {
    font-weight: 500;
    font-size: 2.5625em;
    line-height: 116.4%;
    color: #FFFFFF;
    span {
      color: #E2349D;
    }
  }
  &__btn {
    width: 20.1875em;
    height: 4.375em;
    span {
      font-weight: 600;
      font-size: 1.5625em;
      line-height: 120%;
    }
  }
  &__img {
    display: block;
    max-width: 39.563em;
    margin: 0 0 0 auto;
    img {
      width: 100%;
    }
  }
}
.info {
  padding: 5.625em 0 1.875em;
  &__title {
    text-align: center;
  }
  &__desc {
    max-width: 54.6875em;
    margin: 0.75em auto 0;
    p {
      font-size: 1.3125em;
      line-height: 139.4%;
      text-align: center;
      color: #19304C;
      opacity: 0.8;
    }
  }
  &__img {
    display: block;
    max-width: 53.25em;
    margin: 5.625em auto 0;
    img {
      width: 100%;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.375em;
    max-width: 78.125em;
    margin: 5em auto 0;
  }
  &__item {
    border: 0.125em solid #E2349D;
    border-radius: 1.25em;
    padding: 2.5em 1.875em 3.75em;
    &-img {
      max-width: 9.125em;
      img {
        width: 100%;
      }
    }
    &-title {
      font-weight: 600;
      font-size: 1.625em;
      line-height: 115.4%;
      letter-spacing: -0.01em;
      color: #19304C;
      margin-top: 0.9375em;
    }
    &-desc {
      margin-top: 0.9375em;
      p {
        font-size: 1.125em;
        line-height: 136.9%;
        color: #19304C;
        opacity: 0.8;
        &:not(:first-child) {
          margin-top: 0.9375em;
        }
      }
    }
  }
}
.advantages {
  padding: 1.875em 0;
  &__title {
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.25em;
    grid-auto-rows: 1fr;
    max-width: 78.125em;
    margin: 2.5em auto 0;
  }
  &__item {
    display: flex;
    flex-direction: column;
    &-top {
      flex-grow: 1;
      background: #FFFFFF;
      box-shadow: 0 0.875em 6.3125em rgba(105, 126, 245, 0.24);
      padding: 2.5em 2.1875em;
      border-radius: 1em;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 30px;
        bottom: -30px;
        width: 0;
        height: 0;
        border-top: 2.1875em solid #ffffff;
        border-right: 2.8125em solid transparent;
      }
    }
    &-bottom {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin-top: 2.8125em;
      min-height: 7.5em;
    }
    &-text {
      p {
        font-weight: 500;
        font-size: 1.125em;
        line-height: 134.4%;
        color: #19304C;
      }
    }
    &-title {
      font-weight: 600;
      font-size: 1.125em;
      line-height: 134.4%;
      color: #19304C;
    }
    &-btn {
      width: 12em;
      height: 3.5em;
      margin-top: 0.9375em;
      span {
        font-weight: 600;
        font-size: 1.125em;
        line-height: 134.4%;
      }
    }
  }
}
.project {
  padding: 1.875em 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #FFFFFF;
    box-shadow: 0 1em 9.125em rgba(105, 126, 245, 0.23);
    border-radius: 1.25em;
    padding: 0 2.5em;
    overflow: hidden;
  }
  &__left {
    width: calc(100% - 32.5em);
    padding: 1.5625em 0;
  }
  &__right {
    width: 31.25em;
    margin-left: 1.25em;
  }
  &__desc {
    margin-top: 1.25em;
    p {
      font-size: 1.3125em;
      line-height: 134.4%;
      color: #19304C;
      opacity: 0.8;
    }
  }
  &__btn {
    width: 16.0625em;
    height: 4.375em;
    margin-top: 1.25em;
    span {
      font-weight: 600;
      font-size: 1.5em;
      line-height: 134.4%;
    }
  }
  &__img {
    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
}
.questions {
  padding: 1.875em 0;
  &__title {
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr;
    gap: 2.5em;
    margin-top: 2.8125em;
  }
  &__item {
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: #FFFFFF;
      box-shadow: 0 1em 9.125em rgba(105, 126, 245, 0.05);
      border-radius: 1.25em 1.25em 0 0;
      padding: 1.875em;
      cursor: pointer;
      position: relative;
      z-index: 1;
      span:nth-child(1) {
        font-weight: 500;
        font-size: 2.125em;
        line-height: 134.4%;
        color: #19304C;
      }
      span:nth-child(2) {
        font-weight: 500;
        font-size: 3.75em;
        line-height: 134.4%;
        color: #19304C;
        margin-left: 0.9375em;
      }
    }
    &-hidden {
      background: rgba(255, 255, 255, 0.4);;
      box-shadow: 0px 16px 146px rgba(105, 126, 245, 0.23);
      border-radius: 0 0 1.25em 1.25em;
      padding: 1.875em;
      display: none;
    }
    &-desc {
      p {
        font-size: 1.3125em;
        line-height: 140.9%;
        color: #19304C;
        opacity: 0.8;
        &:not(:first-child) {
          margin-top: 1.25em;
        }
      }
    }
  }
  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 5em;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: -1.25em;
      left: 50%;
      transform: translate(-50%, 0);
      width: 23em;
      height: 0.1875em;
      background: #19304C;
      border: 0.1875em solid #19304C;
    }
    span {
      font-weight: 600;
      font-size: 1.3125em;
      line-height: 122.9%;
      color: #19304C;
      transition: all 0.5s;
      &:hover {
        color: #E2349D;
      }
    }
  }
}
.contacts {
  padding: 1.875em 0;
  &__inner {
    background: linear-gradient(104.73deg, #19304C 8.73%, #793273 109.32%);
    border-radius: 1.25em;
    padding: 5.625em 2.8125em;
  }
  &__title {
    text-align: center;
    color: #ffffff;
  }
  &__form {
    max-width: 45.625em;
    margin: 1.5625em auto 0;
    &--active {
      display: none;
    }
    &-group {
      height: 4.375em;
      margin: 0;
      padding: 0;
      border: none;
      &:not(:first-child) {
        margin-top: 1.5625em;
      }
      input {
        width: 100%;
        height: 100%;
        background: transparent;
        border: none;
        border-bottom: 0.125em solid rgba(197, 203, 210, 0.5);;
        font-size: 1.1875em;
        line-height: 140.9%;
        color: #FFFFFF;
        &::placeholder {
          opacity: 0.5;
        }
      }
    }
    &-btn {
      width: 100%;
      height: 4.375em;
      margin-top: 3.75em;
      span {
        font-weight: 600;
        font-size: 1.5625em;
        line-height: 120%;
      }
    }
  }
  &__thanks {
    width: 100%;
    height: 100%;
    font-weight: 600;
    font-size: 2.125em;
    line-height: 133.4%;
    color: #ffffff;
    justify-content: center;
    align-items: center;
    margin: 1.5625em 0 0;
    display: none;
    &--active {
      display: flex;
    }
  }
}

@media screen and (max-width: 1200px) {
  .main {
    &__inner {
      display: block;
      min-height: 100%;
      padding: 7.8125em 0 2.5em 2.5em;
    }
    &__left {
      width: 100%;
      max-width: 32.5em;
    }
    &__right {
      width: 100%;
    }
    &__subtitle {
      font-size: 1.25em;
    }
    &__title {
      font-size: 2.125em;
    }
    &__btn {
      width: 18.75em;
      height: 3.75em;
      span {
        font-size: 1.25em;
      }
    }
    &__img {
      max-width: 25em;
      margin-top: -6.25em;
    }
  }
  .project {
    &__inner {
      align-items: flex-end;
      padding: 0 0 0 1.875em;
    }
    &__left {
      width: calc(100% - 23.125em);
      padding: 3.75em 0 2.5em;
    }
    &__right {
      width: 21.875em;
    }
    &__img {
      img {
        height: auto;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .info {
    padding-top: 3.125em;
    &__desc {
      p {
        font-size: 1em;
      }
    }
    &__img {
      margin-top: 2.8125em;
    }
    &__items {
      margin-top: 2.8125em;
    }
    &__item {
      padding: 1.5625em 1.5625em 2.5em;
      &-img {
        max-width: 6.875em;
      }
      &-title {
        font-size: 1.1875em;
      }
      &-desc {
        p {
          font-size: 0.8125em;
        }
      }
    }
  }
  .advantages {
    &__item {
      &-top {
        padding: 1.5625em;
        &:after {
          bottom: -1.25em;
          border-top: 1.25em solid #ffffff;
          border-right: 1.5625em solid transparent;
        }
      }
      &-bottom {
        min-height: 5.9375em;
      }
      &-text {
        p {
          font-size: 0.8125em;
        }
      }
      &-title {
        font-size: 0.8125em;
      }
      &-btn {
        width: 9.375em;
        height: 2.8125em;
        span {
          font-size: 0.8125em;
        }
      }
    }
  }
  .project {
    &__desc {
      p {
        font-size: 1em;
      }
    }
    &__btn {
      width: 11.25em;
      height: 3.4375em;
      span {
        font-size: 1.125em;
      }
    }
  }
  .questions {
    &__items {
      gap: 1.25em;
    }
    &__item {
      &-show {
        justify-content: center;
        text-align: center;
        span:nth-child(1) {
          font-size: 1.6875em;
        }
        span:nth-child(2) {
          display: none;
        }
      }
      &-desc {
        text-align: center;
      }
    }
  }
  .contacts {
    &__inner {
      padding: 2.8125em;
    }
    &__thanks {
      font-size: 1.6875em;
    }
  }
}

@media screen and (max-width: 768px) {
  .main {
    &__inner {
      padding: 3.125em 1.25em 0;
    }
    &__right {
      margin-top: 1.875em;
    }
    &__img {
      max-width: 23.75em;
      margin-top: 0;
      margin-right: -1.25em;
      img {
        margin-bottom: -2.5em;
      }
    }
  }
  .project {
    &__inner {
      padding: 2.5em 0.9375em;
    }
    &__left {
      width: 100%;
      margin-right: 0;
      text-align: center;
      padding: 0;
    }
    &__right {
      display: none;
    }
    &__btn {
      margin: 1.25em auto 0;
    }
  }
  .contacts {
    &__form {
      &-btn {
        span {
          font-size: 1.25em;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    &__left {
      max-width: 100%;
      text-align: center;
    }
    &__subtitle {
      font-size: 0.875em;
    }
    &__title {
      font-size: 1.3125em;
    }
    &__btn {
      width: 13.25em;
      height: 3.375em;
      margin: 0 auto;
      span {
        font-size: 0.875em;
      }
    }
  }
  .info {
    &__desc {
      p {
        font-size: 0.75em;
        br {
          display: none;
        }
      }
    }
    &__img {
      margin-top: 1.5625em;
    }
    &__items {
      grid-template-columns: 1fr;
    }
    &__item {
      padding: 1.875em 0.9375em;
      &-img {
        max-width: 5.3125em;
        margin: 0 auto;
      }
      &-title {
        font-size: 0.9375em;
        span {
          color: #E2349D;
        }
      }
      &-desc {
        p {
          font-size: 0.625em;
        }
      }
    }
  }
  .advantages {
    &__items {
      grid-template-columns: 1fr;
    }
  }
  .project {
    &__desc {
      p {
        font-size: 0.75em;
      }
    }
    &__btn {
      width: 9em;
      height: 2.5em;
      span {
        font-size: 0.8125em;
      }
    }
  }
  .questions {
    &__items {
      margin-top: 1.5625em;
    }
    &__item {
      &-show {
        span:nth-child(1) {
          font-size: 1.3125em;
        }
      }
      &-desc {
        p {
          font-size: 0.75em;
        }
      }
    }
    &__link {
      margin-top: 3.75em;
      &:after {
        width: 10.4375em;
      }
    }
  }
  .contacts {
    &__inner {
      padding: 1.875em;
    }
    &__form {
      &-group {
        height: 3.375em;
        &:not(:first-child) {
          margin-top: 0.3125em;
        }
        input {
          font-size: 0.875em;
        }
      }
      &-btn {
        height: 3.375em;
        margin-top: 1.5625em;
        span {
          font-size: 0.875em;
        }
      }
    }
    &__thanks {
      font-size: 1.1875em;
    }
  }
}